<style>
  .chart {
    margin: 0 auto;
    width: 100%;
    max-width: var(--maxWidth);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  figure {
    padding: 0;
    margin: 0;
    display: block;
    visibility: hidden;
  }
  figure.visible {
    visibility: visible;
  }
  .choose {
    display: flex;
    justify-content: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-bottom: 1.5rem;
  }
  button {
    margin: 0 0.25em;
    width: 5rem;
  }

/*# sourceMappingURL=data:application/json;base64, */</style>

<script>
  import Line from "./Line.svelte";
  import LineLabel from "./LineLabel.svelte";
  import AxisY from "./AxisY.svelte";
  import AxisX from "./AxisX.svelte";
  import {
    LayerCake,
    Svg,
    Html,
    flatten,
    calcExtents,
    uniques
  } from "layercake";
  import { ascending, descending } from "d3-array";
  import { format } from "d3-format";

  export let data;
  export let formatTickX;

  const PAD = 8;
  const RATIO = 3;
  const START = new Date("2020-03-19");

  let padding = {
    top: PAD * 2,
    right: PAD * 0.5,
    bottom: PAD * 3,
    left: PAD * 0.5
  };
  let chartW;
  let visible;
  let setting = "deaths";
  let classA = false;

  $: toggle = `${setting}Capita`;
  $: flatData = flatten(data.map(d => d.value)).filter(d => d.dateF >= START);
  $: extents = calcExtents(flatData, fields);

  $: fields = [
    { field: "x", accessor: d => d.dateF },
    { field: "y", accessor: d => d[toggle] }
  ];

  $: chartH = 160;
  $: visible = !!chartW;
  $: yDomain = [0, extents.y[1]];
  $: xDomain = extents.x;

  $: chartData = data.map(d => ({
    ...d,
    value: d.value.filter(v => v.dateF >= START)
  }));

  function formatTickY(d) {
    return format(`.0${toggle === "deathsCapita" ? "0" : "0"}f`)(d);
  }

  function change() {
    if (this.classList[1] !== "active") {
      const { a } = this.dataset;

      if (typeof a !== "undefined") {
        setting = a;
        classA = !classA;
      }
      setting = setting;
    }
  }
</script>

<div class="chart" class:visible>

  <h3 class="center">Cumulative COVID-19 cases in Mass.</h3>
  <div class="choose">
    <button class:active="{!classA}" on:click="{change}" data-a="deaths">
      Deaths
    </button>
    <button class:active="{classA}" on:click="{change}" data-a="cases">
      Cases
    </button>
  </div>
  <figure class:visible style="height: {chartH}px;" bind:clientWidth="{chartW}">
    {#if visible}
      <LayerCake
        {xDomain}
        {yDomain}
        {padding}
        x="dateF"
        y="{toggle}"
        data="{chartData}">
        <Svg>
          <AxisX
            ticks="{[xDomain[0], xDomain[xDomain.length - 1]]}"
            formatTick="{formatTickX}" />
          <AxisY
            tickNumber="{3}"
            formatTick="{formatTickY}"
            suffix="{` ${setting} per 100,000 residents`}" />
          <Line {toggle} />
        </Svg>
        <Html>
          <LineLabel {toggle} />
        </Html>
      </LayerCake>
    {/if}
  </figure>
</div>
